import {
  Drawer,
  DrawerBody,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Box,
  Text,
} from "@chakra-ui/react";
import React from "react";
import { HiOutlineMenuAlt3 } from "react-icons/hi";

export default function NavDrawer() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  return (
    <>
      <Box fontSize={{ sm: "23px" }} onClick={onOpen} color="white">
        <HiOutlineMenuAlt3 />
      </Box>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
        size="full"
      >
        <DrawerOverlay />
        <DrawerContent backgroundColor="#03181a" color="white">
          <DrawerCloseButton color="white" fontSize="lg" />
          <DrawerBody textAlign="center" padding="20px" marginTop="30px">
            <Box
              fontSize={{ base: "22px" }}
              display="flex"
              flexDirection="column"
              height="100%"
            >
              <Box
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                <a href="#home">HOME</a>
              </Box>
              <Box
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                <a href="#about">ABOUT</a>
              </Box>
              <Box
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                <a href="#skills">SKILLS</a>
              </Box>
              <Box
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                <a href="#portfolio">PORTFOLIO</a>
              </Box>
              <Box
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                <a href="#contactMe">CONTACT</a>
              </Box>
              <Text
                cursor="pointer"
                _hover={{ color: "white" }}
                padding="10px"
                onClick={onClose}
              >
                RESUME
              </Text>
            </Box>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
}
