import { Avatar, Box, Text } from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import styles from "../Navbar/Navbar.module.css";
import { BsLinkedin, BsGithub } from "react-icons/bs";
// import { NavLink } from "react-router-dom";
import NavDrawer from "./NavDrawer";
import { HashLink as Link } from "react-router-hash-link";
const Navbar = () => {
  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 20);
    });
  }, []);
  return (
    <>
      <Box
        display={{ base: "none", md: "block", lg: "block" }}
        className={scroll ? styles.active : null}
        position="fixed"
        top="0"
        zIndex="10"
        width="100%"
        p="0"
        m="0"
      >
        <Box
          padding="18px"
          color="gray.400"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          width={{ base: "none", md: "100%", lg: "90%", xl: "80%" }}
          margin="auto"
        >
          {/* <Box
            width="15%"
            cursor="pointer"
            color="#031417"
            fontWeight="bold"
            fontSize={{ base: "none", md: "none", lg: "25px", xl: "25px" }}
          >
            <Box
              backgroundColor="white"
              width="40px"
              height="40px"
              borderRadius="50%"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              S
            </Box>
          </Box> */}
          <Avatar
            name="Sarfraj"
            backgroundColor="white"
            size="md"
            fontWeight="bold"
          />
          <Box
            fontSize={{ md: "12px", lg: "14px" }}
            display="flex"
            justifyContent="space-between"
            width={{ md: "50%", lg: "43%" }}
          >
            <Box cursor="pointer" _hover={{ color: "white" }}>
              <Link smooth to="#home">
                HOME
              </Link>
            </Box>
            <Box cursor="pointer" _hover={{ color: "white" }}>
              <Link
                smooth
                to="#about"
                cursor="pointer"
                _hover={{ color: "white" }}
              >
                ABOUT
              </Link>
            </Box>
            <Box cursor="pointer" _hover={{ color: "white" }}>
              <Link
                smooth
                to="#skills"
                cursor="pointer"
                _hover={{ color: "white" }}
              >
                SKILLS
              </Link>
            </Box>
            <Box cursor="pointer" _hover={{ color: "white" }}>
              <Link
                smooth
                to="#portfolio"
                cursor="pointer"
                _hover={{ color: "white" }}
              >
                PORTFOLIO
              </Link>
            </Box>
            <Box cursor="pointer" _hover={{ color: "white" }}>
              <Link
                smooth
                to="#contactMe"
                cursor="pointer"
                _hover={{ color: "white" }}
              >
                CONTACT
              </Link>
            </Box>
            <Text cursor="pointer" _hover={{ color: "white" }}>
              RESUME
            </Text>
          </Box>
          <Box
            display="flex"
            justifyContent="space-around"
            width="15%"
            fontSize={{ md: "16px", lg: "18px" }}
          >
            <BsLinkedin />
            <BsLinkedin />
            <BsGithub />
          </Box>
        </Box>
      </Box>
      {/* Small screen view */}
      <Box
        display={{ base: "block", md: "none" }}
        className={scroll ? styles.active : null}
        position="fixed"
        top="0"
        zIndex="10"
        width="100%"
      >
        <Box
          width="90%"
          m="auto"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          padding="10px"
        >
          <Avatar
            name="Sarfraj"
            backgroundColor="white"
            size="md"
            fontWeight="bold"
          />

          <NavDrawer />
        </Box>
      </Box>
    </>
  );
};

export default Navbar;
