import { Box, Heading, Image } from "@chakra-ui/react";
import React from "react";
import MyImage from "./sarfraj3.jpg";
const Home = () => {
  return (
    <>
      <Box id="home">
        <Box
          paddingTop={{ base: "90px", md: "110px" }}
          textAlign="center"
          display="flex"
          flexDirection={{ base: "column-reverse", md: "row" }}
          justifyContent="space-between"
          m="auto"
          maxWidth={{ sm: "100%", md: "100%", lg: "79%", xl: "62%" }}
          color="white"
          paddingLeft={{ base: "0", md: "10px" }}
          paddingRight={{ base: "0", md: "50px" }}
        >
          <Box width={{ base: "100%", md: "70%" }} pt="50px">
            <Heading
              fontSize={{ base: "29px", md: "31px", lg: "36px" }}
              display="flex"
              justifyContent="center"
              width={{ base: "100%", md: "80%" }}
              m="auto"
              mt={{ base: "0", md: "15px" }}
            >
              Hi, I'm{" "}
              <Heading
                fontSize={{ base: "29px", md: "31px", lg: "36px" }}
                ml="8px"
                color="#00ccff"
              >
                Sarfraj Ahmad Raza
              </Heading>{" "}
            </Heading>
            <Heading
              fontSize={{ base: "29px", md: "31px", lg: "36px" }}
              mt="10px"
            >
              {" "}
              Full Stack Web Developer{" "}
            </Heading>
          </Box>
          <Box width={{ base: "100%", md: "30%" }}>
            <Box
              height="250px"
              width="250px"
              borderRadius="50%"
              overflow="hidden"
              m="auto"
            >
              <Image src={MyImage} alt="img"></Image>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Home;
